.LearnPage {
	background-color: plum;
	min-height: 90vh;
	display: flex;	
	align-items: center;
	justify-content: center;	
	position: relative;
	padding: 20px 0;

	.StartLearn{
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;		
	}	
	.Learning {
		height: 100%;
		width: 100%;
		font-size: 3rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin: 2rem 0;

		&-words{
			font-size: 5rem;
			display: flex;
			text-align: center;
			@media (max-width: 1000px){
				font-size: 3rem;
			}
		}
		&-sep{
			margin: 0 3rem;
		}
		&-sounds{
			display: flex;
			font-size: 6rem;
			margin: 5rem 0;
			@media (max-width: 1000px){
				font-size: 4rem;
				margin: 3rem 0;
			}
		}
		&-sound{
			cursor: pointer;
			transition: all .5s;
			@media (min-width: 1000px){
				&:hover{
				transform: scale(1.2, 1.2);
			}
			}
			
		}
	
		&-x {
			cursor: pointer;			
			position: absolute;
			top: 50px;
			right: 100px;
			font-size: 5rem;
			@media (max-width: 1000px){
				top: 30px;
				right: 50px;
				font-size: 3rem;
			}
		}
	
	}
	.EndLearn {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		
		&-title{
			font-size: 5rem;
			@media (max-width: 800px){
				font-size: 3rem;
			}
		}
		&-sub{
			font-size: 3rem;
			margin: 2rem 0;
			@media (max-width: 800px){
				font-size: 2rem;
			}
		}

		&-btns{
			display: flex;
			align-items: center;

			@media (max-width: 500px){
				flex-direction: column;
				align-items: center;
				justify-content: center;
				
			}
			.MyBtn{
				margin-bottom: 1rem;
			}
		}
		&-sep{
			width: 3px;
			background-color: #000;
			margin: 0 3rem;
			@media (max-width: 500px){
				display: none;
			}
		}
	}
} 


